import Button from './button'
import ButtonGroup from './button-group'

import Layout from './layout'
import Main from './main'
import Header from './header'
import Aside from './aside'
import Footer from './footer'

import Row from './row'
import Col from './col'

import Input from './input'
import Toast, { registerToast } from './Toast/plugin'

import Tabs from './tabs'
import TabsHead from './tabs-head'
import TabsSpan from './tabs-span'
import TabsBody from './tabs-body'
import TabsPanel from './tabs-panel'

// ÊâÄÊúâÁªÑ‰ª∂ÂàóË°®
const components = {
  Button,
  ButtonGroup,
  Layout,
  Main,
  Header,
  Aside,
  Footer,
  Row,
  Col,
  Input,
  Tabs,
  TabsHead,
  TabsSpan,
  TabsBody,
  TabsPanel
}

const pluginComponent = {
  Toast
}

const install = function (Vue) {
  // Âà§Êñ≠ÊòØÂê¶ÂÆâË£ÖÔºåÂÆâË£ÖËøáÂ∞±‰∏çÁªßÁª≠ÂæÄ‰∏ãÊâßË°å
  if (install.installed) return
  install.installed = true
  // ÈÅçÂéÜÊ≥®ÂÜåÊâÄÊúâÁªÑ‰ª∂
  Object.keys(components).map(key => {
    const cmp = components[key]
    if (typeof cmp === 'undefined' || !cmp.name) return
    Vue.component(cmp.name, cmp)
  })
  // Ê≥®ÂÜåToastÊèí‰ª∂
  registerToast(Vue)
}

export default {
  install,
  ...components,
  ...pluginComponent
}
